.header {
	background-image: url(../img/main-bg.webp);
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 100vh;
	&__wrap {
		position: relative;
		display: flex;
		align-items: center;
		height: 100vh; }
	&__wrap--main-arrow {
		position: absolute;
		right: 0;
		bottom: 100px;
		width: 200px;
		height: 200px;
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			height: 200px;
			transition-duration: 2s; } }
	&__block {}
	&__text {
		color: #f15a25;
		font-size: 17px;
		line-height: 1.55;
		font-weight: 400;
		margin-bottom: 20px; }
	&__title {
		color: #eaeaea;
		font-size: 80px;
		line-height: 1.05;
		font-weight: 500;
		max-width: 770px; } }

@keyframes rotate {
	from {
		transform: rotate(0deg); }
	to {
		transform: rotate(-360deg); } }

.header__wrap--main-arrow img {
	animation-duration: 7s;
	animation-name: rotate;
	animation-iteration-count: infinite;
	animation-timing-function: linear; }
