body {
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Arial',Arial,sans-serif;
	line-height: 1.3;
	letter-spacing: 0.02em;
	background: #fff;
	padding-left: 50px;
	background: url(../img/img-bg-1.jpg) repeat-y center;
	background-size: cover; }

*, *:before, *:after {
	box-sizing: border-box;
	outline: none;
	text-decoration: none;
	margin: 0;
	padding: 0; }

ul,ol {
	list-style: none; }

iframe {
	width: 100%; }
main {
	overflow: hidden; }
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }

.global_hr {
	border-color: #f15a25;
	margin: 20px 0; }
.global_subtitle {
	color: #f15a25;
	font-size: 17px;
	line-height: 1.55;
	font-weight: 400; }
.global_title {
	color: #eaeaea;
	font-size: 80px;
	line-height: 1.05;
	font-weight: 500;
	margin-top: 30px;
	margin-bottom: 50px; }
