.contacts {
	&__wrap {
		padding: 70px 0; }
	&__block {
		padding: 90px 0; }
	&__title {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		color: #eaeaea;
		font-size: 130px;
		line-height: 1.1;
		font-weight: 500;
		svg {
			transform: rotate(-135deg); } }
	&__first-text {
		font-size: 26px;
		color: rgb(241, 90, 37);
		font-weight: 700; }
	&__last-text {
		display: block;
		font-size: 26px;
		color: #fff;
		margin-bottom: 40px;
		&:last-child {
			margin-bottom: 0; } } }
