.nav {
	position: absolute;
	top: 30px;
	width: 100%;
	&__wrap {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__list {
		display: flex; }
	&__item {
		margin-right: 25px; }
	&__link {
		color: #19181d;
		font-size: 17px;
		line-height: 1.55;
		font-weight: 400;
		transition: .2s;
		&:hover {
			color: #f15a25;
			border-bottom: 2px solid #f15a25; } } }
