.nav-bar {
	background: #19181d;
	position: fixed;
	left: 0;
	top: 0;
	height: 100vh;
	width: 50px;
	z-index: 100;
	&__block {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		height: 100vh;
		padding-top: 15px;
		padding-bottom: 50px;
		cursor: pointer;
		z-index: 101; }
	&__btn {
		position: relative;
		width: 26px;
		height: 26px;
		background: transparent;
		border: none;
		cursor: pointer;
		span,
		span::before,
		span::after {
			display: block;
			position: absolute;
			top: calc(50% - 1px);
			left: 0;
			width: 100%;
			height: 1px;
			background: #fff;
			transition: .2s; }
		span::before {
			content: '';
			transform: translateY(8px); }
		span::after {
			content: '';
			transform: translateY(-8px); } }
	&__btn--active {
		span {
			background: transparent; }
		span::before {
			transform: translateY(0px) rotate(-45deg); }
		span::after {
			transform: translateY(-0px) rotate(45deg); } }
	&__title {
		color: #eaeaea;
		font-size: 22px;
		line-height: 1.1;
		font-weight: 400;
		text-transform: uppercase;
		white-space: nowrap;
		transform: rotate(-90deg); }
	&__hidden-block {
		flex-direction: column;
		position: absolute;
		left: 50px;
		top: 0;
		min-width: 245px;
		height: 100vh;
		background: #19181d;
		padding: 20px;
		padding-bottom: 35px;
		opacity: 0;
		transform: translateX(-300px);
		transition-duration: .5s;
		&--active {
			display: flex;
			opacity: 1;
			transform: translateX(0px); } }
	&__logo img {
		width: 150px; }
	&__list {
		margin-top: 5px; }
	&__item {
		padding-top: 10px;
		padding-bottom: 8px; }
	&__link {
    	color: #eaeaea;
	    font-size: 18px;
	    font-weight: 500;
	    opacity: .8;
    	&:hover {
    		color: #f15a25 !important;
    		opacity: 0.6 !important; } } }
