.footer {
	&__wrap {
		padding: 40px 0;
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__first {}
	&__logo {
		width: 124px; }
	&__cry {
		color: #525252;
		font-size: 12px;
		line-height: 1.55;
		font-weight: 400;
		margin-top: 10px; }
	&__last {
		width: 70%; }
	&__list {
		display: flex;
		justify-content: space-between;
		margin-top: 40px; }
	&__item {
		display: flex;
		flex-direction: column;
		&-title {
			color: #525252;
			font-size: 16px;
			line-height: 1.55;
			font-weight: 700;
			letter-spacing: 1px;
			margin-bottom: 15px; }
		&-text {
			color: #ffffff;
			font-size: 17px;
			line-height: 1.55;
			font-weight: 400;
			transition: .2s;
			padding: 5px 0; }
		a:hover {
			color: #f15a25; } } }
