.adv {
	&__wrap {
		padding: 70px 0;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__item {
		display: flex;
		align-items: flex-start;
		width: 50%;
		margin-bottom: 40px;
		&:last-child {
			width: 100%;
			margin-bottom: 0px;
			p:nth-child(2) {
				margin-bottom: 20px; } } }
	&__number {
		color: #303030;
		font-size: 130px;
		line-height: 1;
		font-weight: 500; }
	&__block {
		margin-left: 30px; }
	&__title {
		color: #f15a25;
		font-size: 50px;
		line-height: 1.1;
		font-weight: 500;
		margin-bottom: 40px; }
	&__teext {
		color: #eaeaea;
		font-size: 17px;
		line-height: 1.55;
		font-weight: 400; } }
