    line-height: 1.55;
    font-weight: 400;
    transition: .2s; }
    .nav__link:hover {
      color: #f15a25;
      border-bottom: 2px solid #f15a25; }

.nav-bar {
  background: #19181d;
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 50px;
  z-index: 100; }
  .nav-bar__block {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    padding-top: 15px;
    padding-bottom: 50px;
    cursor: pointer;
    z-index: 101; }
  .nav-bar__btn {
    position: relative;
    width: 26px;
    height: 26px;
    background: transparent;
    border: none;
    cursor: pointer; }
    .nav-bar__btn span,
    .nav-bar__btn span::before,
    .nav-bar__btn span::after {
      display: block;
      position: absolute;
      top: calc(50% - 1px);
      left: 0;
      width: 100%;
      height: 1px;
      background: #fff;
      transition: .2s; }
    .nav-bar__btn span::before {
      content: '';
      transform: translateY(8px); }
    .nav-bar__btn span::after {
      content: '';
      transform: translateY(-8px); }
  .nav-bar__btn--active span {
    background: transparent; }
  .nav-bar__btn--active span::before {
    transform: translateY(0px) rotate(-45deg); }
  .nav-bar__btn--active span::after {
    transform: translateY(0px) rotate(45deg); }
  .nav-bar__title {
    color: #eaeaea;
    font-size: 22px;
    line-height: 1.1;
    font-weight: 400;
    text-transform: uppercase;
    white-space: nowrap;
    transform: rotate(-90deg); }
  .nav-bar__hidden-block {
    flex-direction: column;
    position: absolute;
    left: 50px;
    top: 0;
    min-width: 245px;
    height: 100vh;
    background: #19181d;
    padding: 20px;
    padding-bottom: 35px;
    opacity: 0;
    transform: translateX(-300px);
    transition-duration: .5s; }
    .nav-bar__hidden-block--active {
      display: flex;
      opacity: 1;
      transform: translateX(0px); }
  .nav-bar__logo img {
    width: 150px; }
  .nav-bar__list {
    margin-top: 5px; }
  .nav-bar__item {
    padding-top: 10px;
    padding-bottom: 8px; }
  .nav-bar__link {
    color: #eaeaea;
    font-size: 18px;
    font-weight: 500;
    opacity: .8; }
    .nav-bar__link:hover {
      color: #f15a25 !important;
      opacity: 0.6 !important; }

