.index-products {
	&__wrap {
		position: relative;
		padding: 70px 0; }
	&__list {}
	&__item {
		display: flex;
		padding: 50px 0;
		border-bottom: 2px solid #f15a25;
		&:first-child {
			border-top: 2px solid #f15a25; } }
	&__column {
		&-1 {
			color: #eaeaea;
			font-size: 25px;
			line-height: 1.55;
			font-weight: 700;
			min-width: 30%; }
		&-2 {
			min-width: 55%; }
		&-3 {
			color: #f15a25;
			font-size: 50px;
			line-height: 1.1;
			font-weight: 500;
			margin-left: auto;
			min-width: 15%;
			text-align: right; }
		&-text {
			color: #eaeaea;
			font-size: 50px;
			line-height: 1.1;
			font-weight: 500; }
		&-wrap {
			margin-top: 20px;
			display: flex;
			align-items: center;
			a {
				font-size: 17px;
				line-height: 1.55;
				font-weight: 400;
				border-bottom: 2px solid transparent; }
			a:first-child {
				color: #f15a25;
				&:hover {
					color: #eaeaea;
					border-bottom: 2px solid #f15a25; } }
			a:last-child {
				color: #eaeaea;
				margin-left: 20px;
				&:hover {
					border-bottom: 2px solid #eaeaea; } } } }
	&__images {
		display: inline-flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-top: 100px;
		img {
			max-width: 100%;
			width: calc(66.6% - 10px);
			margin-bottom: 20px;
			&:nth-child(2),
			&:nth-child(3) {
				width: calc(33.3% - 10px); } } } }
