.faq {
	&__wrap {
		padding: 50px 0; }
	&__faq {
		width: 100%;
		padding: 25px 0;
		border-top: 2px solid #f15a25;
		&:last-child {
			border-bottom: 2px solid #f15a25; } }
	&__input,
	&__text-block {
		display: none;
		margin-top: 25px; }
	&__input:checked ~ .faq__text-block {
		display: block; }
	&__label {
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-weight: 600;
		color: #eaeaea;
		font-size: 50px;
		line-height: 1.35;
		cursor: pointer;
		span {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 40px;
			height: 40px;
			background: #f15a25;
			border-radius: 50%;
			transition: .2s; } }
	&__input:checked ~ .faq__label span {
		transform: rotate(-45deg); }
	&__text-block {
		color: #eaeaea;
		font-size: 17px;
		line-height: 1.5;
		max-width: 600px;
		ol {
			list-style-type: decimal;
			padding-left: 20px; }
		p, ul, ol {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } } } }
